@import "./variables.scss";

.activity-card {
  display: flex;
  flex-direction: column;
  height: 190px;
  width: 200px;
  border-radius: 6.9px;
}

.activity-card-title {
  color: white;
  font-size: 20px;
  font-weight: bold;
  margin: 15px 0 0 16px;
}

.activity-card-subheadline {
  font-size: 12px;
  color: #ffffffcb;
  margin-left: 16px;
  letter-spacing: 0px;
}

.activity-card-empty-space {
  flex: 1;
}

.stopwatch {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 16px 10px 10px;
}

.stopwatch-time {
  flex: 1;
  color: #ffffffcb;
  margin-left: 5px;
}

.stopwatch-progress-bar {
  padding-top: 5px; // ?
  margin-top: 2px;
  border-radius: 6px;
  background: white;
  color: white;
}

.black-text {
  color: $text-color;
}

.white-text {
  color: white;
}
